<template>
  <div :ref="elClassName" style="position: relative"></div>
</template>

<script>
import vFramePlayer from  "./vframeplayer.js";

export default {
  name: "FramePlayer",
  props: {
    imgArr: {
      type: Array,
      default: () => [],
      require: true,
    },
    fps: {
      type: Number,
      default: 25,
    },
    useCanvas: {
      type: Boolean,
      default: false,
    },
    loop: {
      type: Number,
      default: 0,
    },
    yoyo: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      elClassName: Math.random().toString(36).slice(-6),
    };
  },
  mounted() {
    const { imgArr, fps, useCanvas, loop, yoyo } = this.$props;

    this.framePlayer = new vFramePlayer({
      dom: this.$refs[this.elClassName],
      imgArr,
      fps,
      useCanvas,
      loop,
      yoyo,
    });
  },
};
</script>

<style lang="stylus">
.ss 
  width 300px
  height 300px
</style>
